.form {
    margin-top: 30px;
    text-align: right;
    display: flex;
    justify-content: space-between;
  }
  
  .form button {
    font: inherit;
    cursor: pointer;
    background-color: #d55757;
    border: 1px solid #8a2b06;
    color: white;
    padding: 0.25rem 2rem;
    border-radius: 20px;
    font-weight: bold;
  }
  
  .form button:hover,
  .form button:active {
    background-color: #2e1207;
    border-color: #641e03;
  }